.main__whatzach{
    display: flex;
    flex-direction: column;
}

.main__whatzach-whozach{
    flex: 1;
    display: flex;
    justify-content: left;
    align-items: left;
    flex-direction: column;
    text-align: left;
    padding-top: 40vh;
}


.main__whatzach-whozach h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 80px;
    line-height: 75px;
    letter-spacing: -.02em;
    color: #222;
}

.main__whatzach-content {
    background-color: rgba(92, 73, 73, 0.144);
    flex: 1;
    display: flex;
    justify-content: left;
    align-items: left;
    flex-direction: column;
    text-align: left;
    padding-top: 2rem;
    width: 50%;
}

.main__whatzach-content p {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 20px;
    line-height: 35px;
    letter-spacing: -.02em;
    color: rgba(214, 205, 205, 0.281);
    padding: 10px;
}