.main__header {
    display: flex;
    flex-direction: row;
}

.main__header-content{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    margin-top: 25vh;
}

.main__header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 90px;
    line-height: 75px;
    letter-spacing: -.02em;

}

.gradient__text span{
    transition: .2s;
}

.gradient__text:hover span {
    margin-right: 2px;
}

.gradient__text:hover span{
    color: #fff;
    text-shadow: 2px 0 3px #fff;
}

.main__header-icons{
    display: flex;
    flex-direction: row;
    margin-top: 3rem;
    justify-content: center;
}

.main__header-icons a{
    margin-right: 3rem;
}

.highlight{
    transition: transform 250ms;
}

.highlight:hover{
    transition: .4s;
    color: white;
    transform: translateY(-10px);
    
}

@media screen and (max-width: 700px) {
    .main__header-content h1 {
        font-family: var(--font-family);
        font-weight: 800;
        font-size: 80px;
        line-height: 75px;
        letter-spacing: -.02em;
    }
  }
